#contact {
    position: relative;
    padding-top: 4rem;
    height: 100%;

    display: flex;
    flex-direction: column;
}

#contact .map {
    cursor: pointer;
    position: relative;
    flex-grow: 1;

    border-top: solid 1px var(--color-gray);
    border-bottom: solid 1px var(--color-gray);

    display: flex;
    align-items: center;
    justify-content: center;
}

#contact .map img {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

#contact .map i {
    z-index: 1;
    height: 2rem;
}

#contact .container {
    padding: 1rem 0;
    margin: auto;

    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    flex-shrink: 0;
}

#contact .item {
    cursor: pointer;

    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: row;
}

#contact .item:not(:first-child) {
    margin-top: .5rem;
}

#contact .item i {
    margin-right: 1rem;

    display: flex;
    align-items: center;
    justify-content: center;
}