#home {
    position: relative;
    height: 100%;

    display: flex;
    flex-direction: column;
}

#home .text {
    z-index: 1;
    padding: 2rem .5rem;
    margin: auto;
    width: 60rem;
    max-width: calc(100% - 2rem);
    height: 20rem;
    border: solid 1px rgba(255, 255, 255, .2);

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    text-align: center;
    color: white;

    backdrop-filter: blur(.5rem);
}

#home .text .slogan {
    font-family: 'JSArirang-Regular', sans-serif;
    font-size: x-large;
}

#home .text .title {
    font-size: xx-large;
    font-weight: 900;
    text-wrap: balance;
    word-break: keep-all;
}

#home .container {
    position: absolute;
    width: 100%;
    height: 100%;
    flex-grow: 1;
}

#home .btn {
    cursor: pointer;
    z-index: 1;
    position: absolute;
    width: 4rem;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: center;

    color: white;
}

#home .btn.prev {
    left: 0;
}

#home .btn.next {
    right: 0;
}

#home .btn.hide {
    display: none;
}

#home .item {
    position: absolute;
    cursor: default;
    width: 100%;
    height: 100%;

    flex-shrink: 0;

    display: flex;
    flex-direction: column;

    transition: calc(var(--ms) * 2) linear;
}

#home .item img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: var(--ms);
}

#home .item video {
    height: 100%;
    object-fit: cover;
    filter: brightness(.9);
}

/* Horizantal */
@media (orientation: landscape) {}

/* Vertical */
@media (orientation: portrait) {
    #home .item video {
        object-position: calc(100% / 3 * 2) 50%;
    }
}