#footer {
    position: relative;
    padding: .5rem;
    height: 25%;
    border-top: solid 1px var(--color-gray);

    text-align: center;
}

/* Horizantal */
@media (orientation: landscape) {
    .h {
        display: block;
    }

    .v {
        display: none;
    }
}

/* Vertical */
@media (orientation: portrait) {
    .h {
        display: none;
    }

    .v {
        display: block;
    }
}