#article {
    position: relative;
    padding-top: 4rem;
    height: 100%;

    display: flex;
    flex-direction: column;
}

#article .container {
    position: relative;
    flex-grow: 1;
    overflow: hidden;
}

#article .background {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: var(--ms);
    filter: brightness(.7);
}

#article .btn {
    cursor: pointer;
    z-index: 1;
    position: absolute;
    bottom: 0;
    width: 25%;

    display: flex;
    align-items: center;
    justify-content: center;
}

#article .btn.prev {
    left: 0;
}

#article .btn.next {
    right: 0;
}

#article .btn.hide {
    display: none;
}

#article .item {
    cursor: default;
    position: absolute;
    bottom: 0;
    height: 75%;
    border: solid 1px var(--color-gray);
    border-bottom: none;
    background-color: white;

    flex-shrink: 0;

    display: flex;
    flex-direction: column;

    transition: calc(var(--ms) * 2);
}

#article .item.active {
    filter: drop-shadow(0 0 1rem rgba(0, 0, 0, .2));
}

#article .item:not(.active) {
    color: white;
    background-color: transparent;
    border-color: transparent;
}

#article .title {
    text-align: center;
    text-wrap: balance;
    word-break: keep-all;
}

#article .item:not(.active) .title {
    text-overflow: ellipsis;
    text-wrap: nowrap;
    overflow: hidden;
}

#article .time {
    margin-bottom: .5rem;
    text-align: center;
}

#article .time i {
    margin-right: .25rem;
}

#article .text {
    padding: 0 .5rem;
    flex-grow: 1;
    border-top: solid 1px var(--color-gray);
    white-space: pre-wrap;
}

#article .text a {
    cursor: pointer;
}

/* Horizantal */
@media (orientation: landscape) {
    #article .btn {
        height: 50%;
        color: white;
    }
    
    #article .item {
        padding: 2rem;
    }

    #article .item:not(.active) .text {
        display: none;
    }
}

/* Vertical */
@media (orientation: portrait) {
    #article .btn {
        height: 6rem;
    }

    #article .item {
        padding: 1rem;
    }
}