#header {
    z-index: 1;
    position: fixed;
    padding: 1rem;
    width: 100%;
    height: 4rem;
    border-bottom: solid 1px transparent;
    overflow: hidden;
    transition: var(--ms);
}

#header.invert:not(:hover):has(.bars.hide)>* {
    filter: invert(1);
}

#header:hover,
#header:has(.bars.show) {
    background-color: white;
    border-bottom: solid 1px var(--color-gray);
}

#header nav {
    width: 100%;
    height: 2rem;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

#logo {
    cursor: pointer;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;

    font-size: x-large;
    font-weight: 900;
}

#logo img {
    margin-right: .5rem;
    height: 100%;
}

#header ul {
    display: flex;
}

#header li {
    cursor: pointer;
    height: 2rem;

    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    transition: var(--ms);
}

#header li:hover {
    transform: scale(1.1);
}

/* Horizantal */
@media (orientation: landscape) {
    #header ul {
        flex-direction: row;
        justify-content: flex-end;
    }

    #header .bars {
        display: none;
    }

    #header li:not(:first-child) {
        padding: 0 1rem;
    }
}

/* Vertical */
@media (orientation: portrait) {
    #header:has(.bars.show) {
        height: 17rem;
    }

    #header nav {
        justify-content: space-between;
    }

    #header ul {
        flex-direction: column;

        display: flex;
        flex-direction: column;
        align-items: flex-end;
    }

    #header .bars {
        width: 2rem;

        display: flex;
        align-items: center;
        justify-content: center;
        flex-shrink: 0;
    }

    #header li:first-child {
        margin-bottom: 1rem;
    }

    #header li:not(:first-child) {
        padding: .5rem 0;
    }

    #header ul:has(.bars.hide) li:not(:first-child) {
        opacity: 0;
    }
}