#project {
    position: relative;
    height: 100%;
}


#project .background {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: calc(var(--ms) * 2);
}

#project .container {
    position: relative;
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: row;
}

#project .btn {
    cursor: pointer;
    z-index: 1;
    position: absolute;
    bottom: 0;
    width: 4rem;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
}

#project .btn.invert {
    filter: invert(1);
}

#project .btn.prev {
    left: 0;
}

#project .btn.next {
    right: 0;
}

#project .btn.hide {
    display: none;
}

#project .item {
    cursor: pointer;
    position: absolute;
    height: 100%;

    display: flex;
    flex-direction: column;
    overflow: hidden;
    flex-shrink: 0;

    transition: calc(var(--ms) * 2);
}

#project .item:hover {
    backdrop-filter: brightness(.4);
}

#project .item img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: calc(var(--ms) * 2);
    filter: brightness(.7);
}

#project .text {
    z-index: 1;
    padding: 0 2rem;
    margin-top: 8rem;
    color: white;
}

#project .text h3 {
    margin: 0;
}

#project .card.first {
    padding-top: 2rem;
    margin-top: 4rem;
    border-top: solid 1px var(--color-gray);
}

#project .card {
    display: flex;
    flex-direction: row;
    font-weight: 200;
}

#project .card>*:first-child {
    width: 4.5rem;
}

/* Horizantal */
@media (orientation: landscape) {
    #project .item.active {
        z-index: 2;
        left: 0 !important;
        width: 100% !important;
    }

    #project .item>* {
        pointer-events: none;
    }

    #project .item img {
        left: -100%;
        width: 300%;
    }

    #project .text *.hover {
        transition: calc(var(--ms) * 2);
        opacity: 0;
    }

    #project .item:hover *.hover {
        opacity: 1;
    }
}

/* Vertical */
@media (orientation: portrait) {
    #project .btn {
        height: 6rem;
    }
}