#company {
    position: relative;
    padding-top: 4rem;
    height: 100%;

    display: flex;
    flex-direction: column;
}

#company .container {
    position: relative;
    flex-grow: 1;
    border-top: solid 1px var(--color-gray);
}

#company .btn {
    cursor: pointer;
    z-index: 1;
    position: absolute;
    top: 0;
    width: 4rem;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
}

#company .btn.invert {
    filter: invert(1);
}

#company .btn.prev {
    left: 0;
}

#company .btn.next {
    right: 0;
}

#company .btn.hide {
    display: none;
}

#company .item {
    position: absolute;
    cursor: default;
    height: 100%;
    background-color: white;

    flex-shrink: 0;

    display: flex;
    flex-direction: column;

    transition: calc(var(--ms) * 2);
}

#company .service .box {
    position: relative;
    padding: 1rem;
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    overflow: hidden;
}

#company .service img {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: brightness(.5);
    transition: calc(var(--ms) * 4);
}

#company .service .box:hover img {
    transform: scale(1.2);
}

#company .service .text {
    z-index: 1;
    text-align: center;
    color: white;
}

#company .service p {
    text-wrap: balance;
    word-break: keep-all;
}

#company .history {
    position: relative;
    padding: 0 1rem;
    margin: auto;
    height: 100%;
    overflow: hidden;
}

#company .history .year {
    position: absolute;

    display: flex;
    flex-direction: column;

    transition: top calc(var(--ms) * 5) linear;
}

#company .history .year i {
    position: absolute;
    left: -1.25rem;
    width: 1rem;
    height: 2.25rem;

    display: flex;
    align-items: center;
    justify-content: center;
}

#company .history .box {
    margin-bottom: 1rem;
    display: flex;
    flex-direction: row;
}

#company .history .card {
    margin-left: .5rem;
    display: flex;
    flex-direction: column;
    text-wrap: balance;
    word-break: keep-all;
}

#company .history h1 {
    margin: 0;
    height: 3rem;
}

#company .history h4 {
    margin: 0;
}

#company .organization {
    flex-grow: 1;
}

#company .organization .box {
    height: 2.5rem;
    display: flex;
    flex-direction: row;
    align-items: flex-end;
}

#company .organization .margin {
    margin-left: 1rem;
    width: 1rem;
    height: 100%;
}

#company .organization .margin div:first-child {
    width: 100%;
    height: calc(50% + .25rem);
}

#company .organization .margin div:last-child {
    width: 100%;
    height: calc(50% - .25rem);
}

#company .organization .bottom {
    border-bottom: solid 1px black;
}

#company .organization .left {
    border-left: solid 1px black;
}

#company .organization .card {
    padding: 0 .5rem;
    height: 2rem;
    border: solid 1px black;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-grow: 1;

    transition: var(--ms);
}

#company .organization .card.lv0 {
    background-color: black;
    color: white;
}

#company .organization .card.lv1 {
    background-color: var(--color-gray);
}


#company .organization h4,
#company .organization small {
    margin: 0;
    line-height: 2rem;
}

#company .subsidiary {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
    flex-shrink: 0;
}

#company .subsidiary h5 {
    margin: 0;
    flex-shrink: 0;
}

#company .subsidiary small {
    margin-left: .5rem;
    word-break: keep-all;
}

#company .ceo {
    height: calc(100% - 4rem);
    border: solid 1px var(--color-gray);
    background-color: white;
    filter: drop-shadow(0 0 1rem rgba(0, 0, 0, .1));

    display: flex;
    flex-direction: column;
}

#company .ceo *:not(p) {
    margin: 0;
    width: 100%;
    display: inline-block;
    text-align: right;
}

#company .ceo p {
    margin: .5rem 0;
    text-wrap: balance;
    word-break: keep-all;
}

#company .ceo div {
    flex-grow: 1;
}

/* Horizantal */
@media (orientation: landscape) {
    #company .item.padding {
        padding: 2rem 8rem;
    }

    #company .service {
        height: 100%;
        display: flex;
        flex-direction: row;
    }

    #company .history {
        width: calc(100% / 3);
        border-left: solid 1px black;
    }

    #company .ceo {
        padding: 2rem 1rem;
    }
}

/* Vertical */
@media (orientation: portrait) {
    #company .btn {
        top: auto;
        bottom: 0;
        height: 6rem;
    }

    #company .item.padding {
        padding: 2rem 1rem;
        height: calc(100% - 4rem);
    }

    #company .service {
        height: 100%;
        display: flex;
        flex-direction: column;
    }

    #company .history {
        margin-top: 0;
        width: 100%;
        height: calc(100% - 6rem);
    }

    #company .ceo {
        padding: 1rem;
        height: 100%;
    }
}